import React from 'react';
import { useSelector } from 'react-redux';
import Item from './Item';

const List = () => {
    const todos = useSelector(state=>state.tiktok.todos)
    return (
        <div>
            {
                todos.map(todo => <Item key={todo.no} todo={todo}/>)
            }
        </div>
    );
};

export default List;