const INSERT = 'reducer/INSERT'
const REMOVE = 'reducer/REMOVE'
const CHANGE = 'reducer/CHANGE'

export const insert = (input) =>({type:INSERT, list:{no:no++, task:input}})
export const remove = (no) =>({type:REMOVE, no})
export const change = (input) =>({type:CHANGE, input})

let no = 3
const initialState = {
    input : 'What you want?',
    todos : [
        {no:1, task:'visiting doctor'},
        {no:2, task:'installing special apps'}
    ]
}

const reducer = (state = initialState, action) => {
    switch ( action.type ){
        case INSERT:
            return {
                ...state,
                todos : [...state.todos, action.list]
            }
        case REMOVE:
            return {
                ...state,
                todos : state.todos.filter(todo => todo.no !== action.no)
            }
        case CHANGE:
            return {
                ...state, input: action.input
            }
        default:
            return state
    }
}

export default reducer