import React from 'react';
import { useDispatch } from 'react-redux';
import { remove } from '../store/reducer';

const Item = ({todo}) => {
    const dispatch = useDispatch()
    const {no, task} = todo
    return (
        <div>
            {no} / {task}
            <button onClick={()=>dispatch(remove(no))}>del</button>
        </div>
    );
};

export default Item;