import React from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { change, insert } from '../store/reducer';

const Input = () => {
    const input = useSelector(state=> state.tiktok.input)
    const dispatch = useDispatch()

    const onSubmit = (e) => {
        e.preventDefault()
        dispatch(insert(input))
    }
    return (
        <form style={{margin: 50}} onSubmit={onSubmit}>
            <input type='text' value={input} onChange={(e)=>dispatch(change(e.target.value))}></input>
        </form>
    );
};

export default Input;